    <div style="margin-bottom: 3%">
      <v-row align="center" justify="center" class="ml-2 mr-2 mt-3">
        <v-col cols="3" sm="3" md="3">
          <v-card class="mt-0" max-width="auto" height="265px" style="border-radius: 15px;">
            <v-container>
              <v-list-item three-line>
                <v-list-item-content>
                  <div class="overline mb-1">Device name</div>
                  <v-card color="red" style="border-radius: 15px;">
                    <v-container>
                      <v-row class="truncate">
                        <v-col>
                          <v-list-item-title
                            class="Subtitle 2 mb-1 white--text font-weight-black ml-1"
