                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card>
                </v-list-item-content>
                <v-list-item-content class="ml-3">
                  <div class="overline mb-1">Department</div>
                  <v-card color="purple accent-2" style="border-radius: 15px;">
                    <v-container>
                      <v-row class="truncate">
                        <v-col>
