                    </v-container>
                  </v-card>
                  <!-- <pre>{{createat}}</pre> -->
                </v-list-item-content>
              </v-list-item>
              <v-list-item three-line>
                <v-list-item-content>
                  <div class="overline mb-1">Machine ID</div>
                  <v-card color="teal accent-4" style="border-radius: 15px;">
                    <v-container>
                      <v-row class="truncate">
                        <v-col>
                          <v-list-item-title
                            class="Subtitle 2 mb-1 white--text font-weight-black ml-1 font"
                            
                            >{{ machineID }}</v-list-item-title
                          >
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card>
                </v-list-item-content>
                <v-list-item-content class="ml-3">
                  <div class="overline mb-1">Device Type</div>
                  <v-card color="blue" style="border-radius: 15px;">
                    <v-container>
                      <v-row class="truncate">
                        <v-col>
                          <v-list-item-title
                            class="Subtitle 2 mb-1 white--text font-weight-black ml-1"
                            >hardware</v-list-item-title
                          >
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card>
                </v-list-item-content>
              </v-list-item>
            </v-container>
          </v-card>
        </v-col>
        <v-col cols="9" sm="9" md="9" style="text-align: -webkit-center">
          <SimpleDonut></SimpleDonut>
        </v-col>
      </v-row>
      <v-row align="center" justify="center" class="ml-2 mr-5">
        <v-col cols="7" style="text-align: -webkit-center">
          <Dashed1></Dashed1>
        </v-col>
        <v-col cols="5" style="text-align: -webkit-center">
          <SemicircleGauge1></SemicircleGauge1>
        </v-col>
      </v-row>

      <v-row align="center" justify="center" class="ml-2 mr-5">
        <v-col cols="7" style="text-align: -webkit-center">
          <Dashed2></Dashed2>
        </v-col>
        <v-col cols="5" style="text-align: -webkit-center">
          <SemicircleGauge2></SemicircleGauge2>
        </v-col>
      </v-row>

      <v-row align="center" justify="center" class="ml-2 mr-5">
        <v-col cols="7" style="text-align: -webkit-center">
          <Dashed3></Dashed3>
        </v-col>
        <v-col cols="5" style="text-align: -webkit-center">
          <SemicircleGauge3></SemicircleGauge3>
        </v-col>
      </v-row>
      <v-btn
        v-scroll="onScroll"
        v-show="fab"
        fab
        dark
        fixed
        bottom
        right
        color="blue"
        @click="toTop"
      >
        <v-icon>mdi-arrow-up-drop-circle</v-icon>
      </v-btn>
    </div>
  </div>
