  },
  methods: {
    API_get_data_meter_donut_pie() {
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/gauge/" + this.machineID,
        headers: { Authorization: "Bearer " + localStorage.token }
      })
        .then(response => {
          // console.log(response.data);
          this.loading = false;
          this.resultcheck = response.data;
          this.result = response.data.result;

          if (this.resultcheck["message"] === "success") {
            var cut_v0 = this.result.V0.toFixed(2);
            var cut_v1 = this.result.V1.toFixed(2);
            var cut_v2 = this.result.V2.toFixed(2);
            this.seriesSemi1.push((cut_v0 / 390) * 100);
            this.seriesSemi2.push((cut_v1 / 390) * 100);
            this.seriesSemi3.push((cut_v2 / 390) * 100);

            var cut_avg =
              (this.result.V0 + this.result.V1 + this.result.V2) / 3;
            this.avgSeriesSemi = cut_avg.toFixed(2);

            var cut_total = this.result.V0 + this.result.V1 + this.result.V2;
            this.totalSeriesSemi = cut_total.toFixed(2);
          } else {
            this.seriesSemi1 = [0];
            this.seriesSemi2 = [0];
            this.seriesSemi3 = [0];
          }
        }) .catch(error => {
          if (error.response.statusText === "UNAUTHORIZED") {
            this.$message.warning("error");
          }else {
            this.seriesSemi1 = [0];
            this.seriesSemi2 = [0];
            this.seriesSemi3 = [0];
          
          }
        });
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("arrayDevice"));
      if (data === null) {
        this.array_getdata = [];
      } else {
        this.array_getdata = data;
      }
      this.machineName = this.array_getdata[0].machineName;
      this.machineID = this.array_getdata[0].machineID;
      this.department = this.array_getdata[0].department;
    }
