      var data_series2 = this.$store.state.data_3.I1.y;
      var data_series3 = this.$store.state.data_3.I2.y;

      var series = [
        {
          name: "Current phase 1",
          data: data_series1
        },
        {
          name: "Current phase 2",
          data: data_series2
        },
        {
          name: "Current phase 3",
          data: data_series3
        }
      ];
      return series;
    },
    chartOptionsDashed3() {
      return {
